import React, { Component, Fragment } from 'react'
import { withStyles } from '@material-ui/styles'
import HeaderTop from '../Components/HeaderTop'
import {Provider} from '../Context'
import SelectMode from '../Components/CourseWizard/SelectMode'
import SelectCourse from '../Components/CourseWizard/SelectCourse'
import CourseWizard from '../Components/CourseWizard/CourseWizard'
import AlertDialog from '../Components/AlertDialog'
import styles from '../Assets/Quick_start_style.js'




const newCourseBasis=window.props.courseBasis


class CreatePage extends Component{
    state={
        translations:window.props.translations,
        courses:window.props.courses || [],
        step:0,
        selectedCourse:undefined,
        mode:"new-course",
        newCourse:newCourseBasis,
        cancelCreationPopup:false,
        newCourseResponse:undefined
    }

    componentWillMount(){
        const referredCourseId = this.props.match.params.referredCourseId
        if(referredCourseId){
         const selectedCourse = this.state.courses.find((course)=>course.course_id===referredCourseId)
         this.setCourse(selectedCourse) 
         this.setState({mode:"re-run", step:1})
        }
    }
    

    setMode=(mode)=>{
        this.setState((prevState)=>({
            mode,
            step:prevState.step+1,
        })
        )
    }

    setCourse=(selectedCourse)=>{
        this.setState((prevstate)=>({
            selectedCourse,
            newCourse:{
                ...prevstate.newCourse,
                course_name:selectedCourse.course_name, 
                org:selectedCourse.org
            }
        }))
    }

    addCourseDetails = (courseDetail)=>{
        this.setState((prevstate)=>({
            newCourse:{...prevstate.newCourse, [courseDetail.name]:courseDetail.value }
        }))
    }

    handleNextClick= () =>{
        this.setState((prevState)=>({
            step:prevState.step+1
        })
        )
    }

    checkCourseId = async () => {
        const {newCourse}=this.state;
        const url = window.props.api.courseidChecking+"course-v1:"+newCourse.org.toString()+"+"+newCourse.course_number.toString()+"+"+newCourse.course_session.toString()
        const settings = {
            method:'GET',
            headers:{
                Accept:'application/json',
                'Content-Type':'application/json'
            }
        }
        const api_call = await fetch(url, settings)
        const data = await api_call.json()
        const validId = data.details==="valid_new_id" 
        this.setState((prevState)=>(
            {newCourse:{...prevState.newCourse, validCourseId:validId}}
        ))
        validId && this.handleNextClick()
        return 
    }

    launchCourseCreation = async (e) => {
        e.preventDefault();
        this.handleNextClick();

        const url = window.props.api.courseCreationUrl
        const settings = {
            method:'POST',
            body :this.getFormData(),
            contentType: false,
            processData: false,
        }
        const api_call = await fetch(url, settings)
        const data = await api_call.json();
        if(data.cod!=='404'){
            this.setState({newCourseResponse:data})
        }
    }

    getCourseEffort =()=>{
        return (this.state.newCourse.effort[0]*3600 || 0)+(this.state.newCourse.effort[1]*60 || 0)
    }

    getFormData = ()=>{
        let formData = new FormData();
        Object.entries(this.state.newCourse).forEach((courseInfo)=>{
            console.log(courseInfo)
            if(courseInfo[0]==="course_image" && courseInfo[1]){
                formData.append('course_image', this.state.newCourse.course_image[0])
            }
            else if(courseInfo[0]==="teacher_image" && courseInfo[1]){
                formData.append('teacher_image', this.state.newCourse.teacher_image[0])
            }
            else if(courseInfo[0]==="effort"){
                formData.append('effort', this.getCourseEffort())
            }
            else if(courseInfo[0]==="course_map"){
                formData.append('course_map', JSON.stringify(this.state.newCourse.course_map))
            }
            else{
                formData.append(courseInfo[0], courseInfo[1])
            }
        })
        return formData
    }

    handleBackClick = () =>{
        this.setState((prevState)=>({
            step:prevState.step-1
        }))
    }

    handleFieldChange=(fieldName, fieldValue)=>{
        if(['course_session','course_number'].includes(fieldName)){
            fieldValue=fieldValue.replace(/ /g,'').replace(/[^a-zA-Z0-9]/g, "").toLowerCase()
        }
        this.setState((prevState) => {
            return {newCourse:{ ...prevState.newCourse, [fieldName]:fieldValue, validCourseId:true}};
        });

    }

    toggleDialog = () =>{
        this.setState((prevstate)=>(
            {
                cancelCreationPopup:!prevstate.cancelCreationPopup
            }
        ))
    }

    eraseNewCourse=()=>{
        this.setState({
            newCourse:newCourseBasis,
            step:0,
            cancelCreationPopup:false,
            selectedCourse:undefined,
            mode:undefined,
        })
    }

    getContext=()=>{
        return{
            ...this.state,
            handleFieldChange:this.handleFieldChange,
            handleNextClick:this.handleNextClick,
            addCourseDetails:this.addCourseDetails,
            toggleDialog:this.toggleDialog,
            handleBackClick:this.handleBackClick,
            launchCourseCreation:this.launchCourseCreation,
            checkCourseId:this.checkCourseId,
            eraseNewCourse:this.eraseNewCourse
        }
    }
    

    render(){
        const {classes} = this.props,
            {step, mode, selectedCourse, translations}=this.state
        const courses = this.state.courses.filter((course)=>course.type==="phileas")
        return(
            <Fragment>
                <Provider value={this.getContext()}>
                    <HeaderTop/>
                    <div className={classes.createPageWrapper}>
                        {step===0 && <SelectMode setMode={this.setMode}/>}
                        {(step===1 && mode==="re-run" && !selectedCourse) && <SelectCourse courses={courses} setCourse={this.setCourse}/> }  
                        {([1,2,3,4,5,6].includes(step) && (mode==="new-course"|| selectedCourse) ) && <CourseWizard/> }
                    </div>
                    <AlertDialog 
                        openDialog={this.state.cancelCreationPopup}
                        toggleDialog={this.toggleDialog}
                        dialogTitle={translations.cancelCreationTitle}
                        dialogContent={translations.cancelCreationContent}
                        dialogValid={translations.validCancelCreation}
                        dialogCancel={translations.backToCourse}
                        dialogAction={this.eraseNewCourse}
                    />
                </Provider>
            </Fragment>
        )
    }
}




export default withStyles(styles)(CreatePage);