import React from 'react'
import Loader from 'react-loader-spinner';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { Context } from '../../Context';
import CheckCircleOutline from '@material-ui/icons/CheckCircleOutline';
import HighlightOff from '@material-ui/icons/HighlightOff'
import { Link } from "react-router-dom";
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/styles';


function LoadingPage({classes}){
    return(
        <Context.Consumer>
            {({newCourseResponse, translations}) =>(
            <div className={classes.loadingPage}>
                {
                    newCourseResponse
                    ?
                    <CourseCreationResult/>
                    :<>
                        <h3>{translations.waitCourseCreation}</h3>
                        <Loader 
                        type="Puff"
                        color={window.props.colors.mainColor}
                        height="100"	
                        width="100"
                        />   
                    </>
                }
            </div>
            )}
        </Context.Consumer>
    )
}  
export default withStyles(styles)(LoadingPage)


const CourseCreationResult = (props)=>{
    return(
        <Context.Consumer>
        {({newCourseResponse, newCourse, eraseNewCourse, translations}) =>( 
              <>
              {newCourseResponse.status!=="error"
              ?<>
                  <div><CheckCircleOutline style={{fontSize:90, color:window.props.colors.mainColor}}/></div>
                  <h3>{translations.courseHasBeenCreated}</h3>
                  <h4>{translations.stepFields.course_name_title} : {newCourse.course_name}</h4>
                  <Grid container 
                      spacing={6}
                      direction="row"
                      justify="center"
                      alignItems="center"
                  >
                      <Grid item>
                          <Button href={newCourseResponse.edit_link} onClick={()=>eraseNewCourse()}>
                              {translations.seeMyCourse}
                          </Button>
                      </Grid>
                      <Grid item>
                      <Link to="/">
                          <Button onClick={()=>eraseNewCourse()}>
                              {translations.backToHomePage}
                          </Button>
                      </Link>
                      </Grid>
                  </Grid>
              </>
              :
              <>    
              <div><HighlightOff style={{fontSize:90, color:"red"}}/></div>
              <h3>{translations.errorWhileCreatingCourse}</h3>
              <h4>{translations.courseNotCreated}</h4>
              <Grid container 
                  spacing={6}
                  direction="row"
                  justify="center"
                  alignItems="center"
              >
                  <Grid item>
                     <Link to="/">
                          <Button onClick={()=>eraseNewCourse()}>
                              {translations.backToHomePage}
                          </Button>
                      </Link>
                  </Grid>
              </Grid>
              </>
              }
          </>
        )}
        </Context.Consumer>
      
    )
}

