import 'date-fns';
import React from 'react';
import TextField from '@material-ui/core/TextField';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import Checkbox from '@material-ui/core/Checkbox';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import Grid from '@material-ui/core/Grid';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Slider from '@material-ui/lab/Slider';
import NumberFormat from 'react-number-format';
import { withStyles, Button } from '@material-ui/core';
import {DropzoneArea} from 'material-ui-dropzone';
import styles from '../../Assets/Quick_start_style.js';

import AddCircleOutline from '@material-ui/icons/AddCircleOutline';
import Clear from '@material-ui/icons/Clear';


const FileUpload =withStyles(styles)(({field, translations,classes, addCourseDetails, value}) =>{
    const handleFieldChange=(file)=>{
        addCourseDetails({name:field.name, value:file})
    }
    return (
    <div className={classes.mediumWrapper+' '+classes.fieldWrapper}>
        <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+"_title"]}</h3>
        <p>{translations.stepFields[field.name+"_helper"]}</p>
        <Grid 
            container
            direction="row"
            justify="center"
            alignItems="center"
        >
            <Grid item xs={6}>
                <DropzoneArea 
                onChange={(e)=>handleFieldChange(e)}
                filesLimit={1}
                acceptedFiles={['image/*']}
                dropzoneText={translations.dragAndDropInfo}
                showAlerts={false}
                showPreviewsInDropzone={true}
            />
            </Grid>
            <Grid item xs={6}>
                {(value && value[0]) && <img style={{maxWidth:"100%",padding:20}} src={URL.createObjectURL(value[0])} alt="course illustration"/>}
            </Grid>
        </Grid>
    </div>
    )  
})


const TimeInput = withStyles(styles)(({field, translations,classes, addCourseDetails, value})=>{
    const handleFieldChange=(name, newValue)=>{
        console.log("change", newValue, field.name)
        name==="hours"
        ? addCourseDetails({name:field.name, value:[newValue, value[1]]})
        : addCourseDetails({name:field.name, value:[value[0], newValue]})
    }
    return(
        <>
        <div className={[classes.fieldWrapper,classes.smallWrapper].join(' ')}>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+"_title"]}</h3>
            <p>{translations.stepFields[field.name+"_helper"]}</p>
            <Grid container>
                <Grid item xs={6}>
                    <NumberFormat value={value[0]} onChange={e=>handleFieldChange("hours", e.target.value)} className={classes.timeInput}/>
                    <span className={classes.inputLabel}>{translations.hours}</span>
                </Grid>
                <Grid item xs={6}>
                    <NumberFormat value={value[1]}  onChange={e=>handleFieldChange("minutes", e.target.value)} className={classes.timeInput}/>
                    <span className={classes.inputLabel}>{translations.minutes}</span>
                </Grid>
            </Grid>

        </div>
        </>
    )
})

const TextInput =withStyles(styles)( ({field, translations, defaultValue, handleFieldChange, fixedField, classes, newCourse})=>{
    const wrongId = ['course_session',"course_number"].includes(field.name) && !newCourse.validCourseId
    return(
        <>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+"_title"]}{field.required && "*"}</h3>
            <p>{translations.stepFields[field.name+"_helper"]}</p> {fixedField && <p>{translations.stepFields['rerunWarning']}</p>}
            <TextField
                defaultValue={translations.stepFields[field.name+"_default_value"]}
                margin="normal"
                value={defaultValue}
                InputProps={fixedField ? {readOnly:true} : {}}
                onChange={e=>handleFieldChange(field.name, e.target.value)}
                variant="outlined"
                error={wrongId} 
            />
            {wrongId &&
                <FormHelperText error id="component-error-text">{translations.invalidCourseId} </FormHelperText>
            }
        </>
    )
})

const RadioInput = withStyles(styles)(({field, translations, classes, addCourseDetails, value}) =>{
    const handleChange = name => event => {
        addCourseDetails({"value":event.target.value, name:field.name})
    };
    return(
        <div className={classes.fieldWrapper}>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <p>{translations.stepFields[field.name+'_helper']}</p>
            <FormControl component="fieldset">
                <RadioGroup
                aria-label={field.name}
                name={field.name}
                value={value || field.options[0]}
                onChange={handleChange(field.name)}
                >
                    {field.options.map((option, key)=>
                        <div key={key}> 
                            <FormControlLabel value={option} control={<Radio />} label={translations.stepFields[option+"_title"] || option}/>
                            <FormHelperText>{translations.stepFields[option+"_helper"]}</FormHelperText>
                        </div>
                    )}
                </RadioGroup>
            </FormControl>
        </div>
    )
})

const CheckBoxInput = withStyles(styles)( ({field, translations, classes, addCourseDetails, value}) =>{
    const handleChange = name => event => {
        addCourseDetails({"name":name, "value":event.target.checked})
    };
    return(
        <div className={classes.fieldWrapper}>
           <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <p>{translations.stepFields[field.name+'_helper']}</p>
            <FormControl component="fieldset">
                <FormGroup>
                {field.options.map((option, key)=>
                    <div key={key}>
                        <FormControlLabel
                        control={<Checkbox value={option} onChange={handleChange(option)} checked={value[option]}/>}
                        label={translations.stepFields[option+"_title"] || option}
                        />
                        <FormHelperText>{translations.stepFields[option+"_helper"]}</FormHelperText>
                    </div>
                )}
                </FormGroup>
            </FormControl>
        </div>
    )
})

function DateTimePicker({type, translations, value, addCourseDetails}) {
    function handleDateChange(date) {
        addCourseDetails({"value":date, name:type})
    }
  
    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <Grid container justify="space-around">
          <KeyboardDatePicker
            margin="normal"
            id="mui-pickers-date"
            label={type==="startDate"?translations["startDate_date"] : translations["endDate_date"]}
            value={value}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change date',
            }}
            cancelLabel={translations.cancel}
            okLabel={translations.confirm}
          />
          <KeyboardTimePicker
            margin="normal"
            id="mui-pickers-time"
            label={type==="startDate"?translations["startDate_time"] : translations["endDate_time"]}
            value={value}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change time',
            }}
            cancelLabel={translations.cancel}
            okLabel={translations.confirm}
          />
        </Grid>
      </MuiPickersUtilsProvider>
    );
  }

  const SelectInput =withStyles(styles)( ({value='', field, translations, classes, addCourseDetails})=>{
    const handleChange =  event => {
        addCourseDetails({"value":event.target.value, name:field.name})
    };
      return(
        <div className={classes.fieldWrapper}>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <FormHelperText>{translations.stepFields[field.name+'_helper']}</FormHelperText>
            <FormControl variant="outlined">

            <Select
            value={value}
            onChange={handleChange}
            inputProps={{
                name: field.name,
            }}
            className={classes.selectField}
            >
            <MenuItem value="">
                <em></em>
            </MenuItem>
            {field.options.map((option, key)=><MenuItem key={key} value={option}>{translations.stepFields[option+"_title"] || option}</MenuItem>)}
            </Select>
        </FormControl>
      </div>
      )
  })

const TextAreaInput = withStyles(styles)(({value, field, translations, classes, addCourseDetails})=>{
    const handleChange =  event => {
        addCourseDetails({"value":event.target.value, name:field.name})
    };
    return(
        <div className={classes.fieldWrapper}>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <FormHelperText>{translations.stepFields[field.name+'_helper']}</FormHelperText>
            <TextField rows="4" className={classes.textEditorWrapper} multiline variant="outlined" onChange={handleChange} value={value}/>
        </div>
    )
})


const StyledSlider = withStyles({
    thumb: {
      height: 24,
      width: 24,
      backgroundColor: '#fff',
      marginTop:10,
    },
    track: {
      backgroundColor: window.props.colors.configureColor,
      height: 24,
      borderRadius:60
    },
    trackAfter: {
        backgroundColor: window.props.colors.statisticsColor,
        opacity:1,
        borderRadius:60
      },
    container:{
        height:70
    }
  })(Slider);

const GradeInput = withStyles(styles)(({value, field, translations, classes, addCourseDetails})=>{
    const handleChange =  (event, newValue) => {
        addCourseDetails({"value":Math.round(newValue), name:field.name})
    };

    const createGradeBasis = ()=>{
        let base = []
        for (let i = 0; i <= 100; i+=10) {
            base.push(<span key={i}>{i}</span>)
        }
        return base
    }


    return(
        <div className={classes.fieldWrapper}>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <FormHelperText>{translations.stepFields[field.name+'_helper']}</FormHelperText>
            <p>{translations.fail || "Fail Level"} : 0% - {Math.round(value)}% </p>
            <p>{translations.pass || "Pass"} : {Math.round(value)}% - 100% </p>
            <StyledSlider container={{height:60}} value={value} onChange={handleChange} className={classes.sliderWrapper}/>
            <Grid 
                container
                direction="row"
                justify="space-between"
                alignItems="center"
            >
                {createGradeBasis()}
            </Grid>
        </div>
    )
})


const JsonEditor = withStyles(styles)(({classes, value, field, translations, addCourseDetails})=>{

    const handleChange =(newValue, sectionIndex, subsectionIndex=undefined)=>{
        let finalValue = [...value]
        if(subsectionIndex!==undefined){
            finalValue[sectionIndex]['subsections'][subsectionIndex]=newValue
        }
        else{
            finalValue[sectionIndex]["title"]=newValue
        }
        addCourseDetails({name:field.name, value:finalValue})
    }

    const addSection = ()=>{
        addCourseDetails({name:field.name, value:[...value, {"title":translations.new_section || "New Section", "subsections":[translations.new_subsection || "New Subsection"]}]})
    }

    const addSubSection = (sectionIndex)=>{
        let modifiedValue = [...value]
        modifiedValue[sectionIndex]['subsections'].push(translations.new_subsection || "New subsection")  
        addCourseDetails({name:field.name, value:modifiedValue})
    }

    const deleteElement=(sectionIndex, subsectionIndex=undefined)=>{
        let modifiedValue=[...value]
        if(subsectionIndex!==undefined){
            if(modifiedValue[sectionIndex]["subsections"].length>1){
                modifiedValue[sectionIndex]["subsections"]=[...modifiedValue[sectionIndex]["subsections"].splice(0, subsectionIndex), ...modifiedValue[sectionIndex]["subsections"].splice(subsectionIndex+1)]
            }
            else{
                modifiedValue[sectionIndex]["subsections"]=[]
            }
        }
        else{
            modifiedValue= [...modifiedValue.splice(0, sectionIndex), ...modifiedValue.splice(sectionIndex+1)]
        }
        addCourseDetails({name:field.name, value:modifiedValue})
    }

    return(
        <div className={classes.fieldWrapper}>
        <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
        <FormHelperText className={classes.helpers}>{translations.stepFields[field.name+'_helper']}</FormHelperText>

        <Grid 
        container
        direction="column"
        justify="center"
        alignItems="flex-start"
        spacing={2}
        >
        {value.map((mapElement, key1)=>{
            return(
                <div  key={key1}>
                    <Grid item>
                        <TextField value={mapElement['title']} variant="outlined" onChange={(e)=>handleChange(e.target.value, key1)}/>
                        <Clear className={classes.clear} onClick={()=>deleteElement(key1)}/>
                    </Grid>
                    {mapElement['subsections'].map((subsection, key2)=> 
                        <Grid item key={key2}  className={classes.subsections} >
                            <TextField value={subsection} variant="outlined" onChange={(e)=>handleChange(e.target.value, key1, key2)}/>
                            <Clear className={classes.clear} onClick={()=>deleteElement(key1, key2)}/>
                        </Grid>)}
                    <span className={classes.sectionsAdd} onClick={(e)=>addSubSection(key1)} >
                        <AddCircleOutline/> {translations.addSubsection || "Add Subsection"}
                    </span>
                </div>   
                ) 
        })}
        <span className={classes.sectionsAdd} onClick={()=>addSection()} ><AddCircleOutline/> {translations.addSection || "Add Section"}</span>
        </Grid>
    </div>
    )
})




export {
    TextInput,
    RadioInput,
    CheckBoxInput,
    DateTimePicker,
    SelectInput,
    TextAreaInput,
    GradeInput,
    TimeInput,
    FileUpload,
    JsonEditor
}