import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { withStyles } from '@material-ui/styles';
import styles from '../Assets/Quick_start_style.js';

function AlertDialog({
  openDialog, 
  toggleDialog, 
  dialogTitle, 
  dialogContent, 
  dialogValid, 
  dialogCancel, 
  dialogAction,
  classes
}) {
  return (
    <div>
      <Dialog
        open={openDialog}
        onClose={toggleDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{dialogTitle}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {dialogContent}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          {
            dialogValid &&
            <Button onClick={dialogAction} color="primary" className={classes.dialogButtons}>
              {dialogValid}
            </Button>
          }
          {dialogCancel &&
            <Button onClick={toggleDialog} color="primary" autoFocus className={classes.dialogButtons}>
              {dialogCancel}
            </Button>
          }
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default withStyles(styles)(AlertDialog);