import React from 'react';
import Moment from 'react-moment';
import 'moment/locale/fr';


function CourseSummary({newCourse, translations,classes}) {
  const getCourseInfoValue = (courseInfo)=>{
    let courseInfoOutput;
    if(courseInfo[1] instanceof Date){
        courseInfoOutput= <Moment locale={window.props.currentLanguage==="fr"? "fr" : "en"} format={"LLL"}>{courseInfo[1].toString()}</Moment>
    }
    else if(courseInfo[0]==="course_grade"){
        courseInfoOutput= newCourse['is_course_graded']?<span>{courseInfo[1]}%</span> : <span>{translations.courseNotGraded}</span>
    }
    else if(courseInfo[0]==="effort"){
      courseInfoOutput = <span> {courseInfo[1][0]} {translations.hours} {courseInfo[1][1]} {translations.minutes} </span>
    }
    else if(courseInfo[0]==="course_image"){
      courseInfoOutput = courseInfo[1] 
      ? <div><img style={{maxWidth:150}} src={URL.createObjectURL(courseInfo[1][0])} alt="course illustration"/></div>
      :<span>{translations.noImage}</span>
    }
    else if(courseInfo[0]==="teacher_image"){
      courseInfoOutput = courseInfo[1] 
      ? <div><img style={{maxWidth:150}} src={URL.createObjectURL(courseInfo[1][0])} alt="teacher illustration"/></div>
      :<span>{translations.noImage}</span>
    }
    else if(courseInfo[0]==="course_map"){
      courseInfoOutput = courseInfo[1] 
      ?courseInfo[1].map((section)=> {
        return(
          <>
            <p>{section['title']}</p>
            {section['subsections'].map((subsection)=><p>{subsection}</p>)}
          </>
        )
      })
      : <span>{translations.no_course_map || "No course map provided"}</span>
    }
    else{
      courseInfoOutput= 
        typeof courseInfo[1] ==="boolean" 
        ? <span>{translations[courseInfo[1].toString()]}</span>
        : <span>{translations.stepFields[courseInfo[1]+"_title"] || courseInfo[1]}</span>
    }

    if(courseInfoOutput){
      return (
        <>
            <span className={classes.courseSummaryTitle}>{translations.stepFields[courseInfo[0]+"_title"] || courseInfo[0]} : </span> 
            {courseInfoOutput}
        </>
        )  
    }
  }  

  return (
    <div>
        <h3>{translations.verifyCourseInfo}</h3>
        {Object.entries(newCourse).map((courseInfo, key)=>
            <div key={key}>
                {getCourseInfoValue(courseInfo)}
            </div>)}
    </div>    
  );
}

export default CourseSummary;