import React from 'react'
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';
import styles from '../../Assets/Quick_start_style.js';
import {Context} from '../../Context'


const Cartouche = ({classes})=>{
    return(
        <Context.Consumer>
            {({newCourse, translations})=>(
            <div className={classes.cartouche}>
                <Grid 
                    container
                    direction="row"
                    justify="center"
                    alignItems="center"
                >
                    <Grid item xs={3}>
                        <p className={classes.cartoucheTitle}>{translations.stepFields.course_name_title}</p>
                        <p className={classes.cartoucheInfo}>{newCourse.course_name}</p>
                    </Grid>
                    <Grid item xs={3}>
                        <p className={classes.cartoucheTitle}>{translations.stepFields.org_title}</p>
                        <p className={classes.cartoucheInfo}>{newCourse.org}</p>
                    </Grid>
                    <Grid item xs={3}>
                        <p className={classes.cartoucheTitle}>{translations.stepFields.course_number_title}</p>
                        <p className={classes.cartoucheInfo}>{newCourse.course_number}</p>
                    </Grid>
                    <Grid item xs={3}>
                        <p className={classes.cartoucheTitle}>{translations.stepFields.course_session_title}</p>
                        <p className={classes.cartoucheInfo}>{newCourse.course_session}</p>
                    </Grid>
                </Grid>
            </div>
            )}
        </Context.Consumer>
    )
}


export default withStyles(styles)(Cartouche)