import React from 'react'
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import CollapsableFilter from "./CollapsableFilter";
import { Context } from '../Context';
import styles from '../Assets/Quick_start_style.js'


function SideBar({classes}){
    return(
        <Context.Consumer>
            {({filters, resetFilters})=>(
                <Paper className={classes.sideBarWrapper}>
                <div className={classes.sideBarTitleWrapper}>
                    <div className={classes.resetWrapper}>
                        <button className={classes.buttonReset} onClick={()=>resetFilters()}>Reset</button>
                    </div> 
                    <p className={classes.sideBarTitle}>Add filter</p>
                    <p>Select filters you want for your search</p>
                </div>
                <div>
                    {filters.map((filter, index)=><CollapsableFilter key={index} filter={filter}/>)}
                </div>
                </Paper>
            )}
        </Context.Consumer>
    )

}

export default withStyles(styles)(SideBar)

