import React, { Component } from 'react'
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import {
    TextInput, 
    RadioInput, 
    CheckBoxInput, 
    DateTimePicker, 
    SelectInput,
    TextAreaInput, 
    GradeInput, 
    TimeInput, 
    FileUpload,
    JsonEditor
} from './CreateFields'
import Button from '@material-ui/core/Button';
import Cartouche from './Cartouche';
import CourseSummary from './CourseSummary';
import LoadingPage from './LoadingPage'
import styles from '../../Assets/Quick_start_style';
import {Context} from '../../Context';


class CourseWizard extends Component{
    state={
        step1:[
            {
                name:"course_name",
                required:true,
                type:"text"
            },
            {
                name:"org",
                required:true,
                type:"text"
            },
            {
                name:"course_number",
                required:true,
                type:"text"
            },
            {
                name:"course_session",
                required:true,
                type:"text"
            }
        ],
        step2:[
            {
                name:"course_pacing",
                type:"radio",
                options:[
                    'instructor_paced',
                    'self_paced'
                ]
            },
            {
                name:"effort",
                type:"time"
            },
            {
                name:"course_dates",
                type:"date",

            },
            {
                name:"language",
                type:"select",
                options:[
                    'fr',
                    'en',
                    'de',
                    'es',
                    'it'
                ]
            },
            {
                name:"course_type",
                type:"checkBox",
                options:[
                    'is_invitation_only',
                    'is_manager_only',
                    'is_mandatory'
                ]
            }
        ],
        step3:[
            {
                name:"course_tag",
                type:"select",
                options:[
                    'computer science',
                    'finance',
                    'management',
                    'desktop',
                    'design'
                ]
            },
            {
                name:"course_onboarding_tag",
                type:"select",
                options:[
                    "onboarding_1",
                    "onboarding_2",
                    "onboarding_3"
                ]
            },
            {
                name:"course_image",
                type:"image"
            },
            {
                name:"course_about",
                type:"textField"
            },
            {
                name:"course_map",
                type:"jsonEditor"
            },
            {
                name:"teacher_image",
                type:"image"
            },
            {
                name:"teacher_name",
                required:false,
                type:"text"
            },
            {
                name:"teacher_email",
                required:false,
                type:"text"
            }
        ],
        step4:[
            {
                name:"course_settings",
                type:"checkBox",
                options:[
                    'is_course_graded',
                    'has_menu'
                ]
            },
            {
                name:"course_grade",
                type:"grade"
            }
        ]
    }    



    createFields=(fields=[], selectedCourse={})=>{
        const {handleFieldChange, newCourse, translations, addCourseDetails}=this.context
        const {classes}=this.props
        return fields.map((field, key)=>{
            switch(field.type){
                case "text":
                    return <TextInput key={key} field={field} translations={translations} defaultValue={newCourse[field.name]||''} handleFieldChange={handleFieldChange} fixedField={selectedCourse[field.name] && true} newCourse={newCourse}/>
                case "radio":
                    return <RadioInput key={key} field={field} translations={translations} addCourseDetails={addCourseDetails} value={newCourse[field.name]}/>
                case "checkBox":
                    return <CheckBoxInput key={key} field={field} translations={translations} addCourseDetails={addCourseDetails} value={newCourse}/>
                case "date":
                    return <div key={key} className={classes.mediumWrapper+' '+classes.fieldWrapper}>
                    <h3 className={classes.fieldTitle}>{translations.stepFields['course_dates_title']}</h3>
                    <p>{translations.stepFields['course_dates_title_helper']}</p>
                    <DateTimePicker type="startDate" addCourseDetails={addCourseDetails} translations={translations} value={newCourse["startDate"]}/> 
                    <DateTimePicker type="endDate" addCourseDetails={addCourseDetails}  translations={translations} value={newCourse["endDate"]}/>
                    </div>
                case "select":
                    return(
                        <SelectInput key={key} field={field} translations={translations} addCourseDetails={addCourseDetails} value={newCourse[field.name]}/>
                    )
                case "textField":
                    return(
                        <TextAreaInput key={key} field={field} translations={translations} addCourseDetails={addCourseDetails} value={newCourse[field.name]}/>
                    )
                case "grade":
                    return(
                        <GradeInput key={key} field={field} translations={translations} addCourseDetails={addCourseDetails} value={newCourse[field.name]}/>
                    )
                case "time":
                return(
                    <TimeInput key={key} field={field} translations={translations} addCourseDetails={addCourseDetails} value={newCourse[field.name] || []}/>
                )
                case "image":
                    return(
                        <FileUpload  key={key} field={field} translations={translations} addCourseDetails={addCourseDetails} value={newCourse[field.name]}/>
                    )
                case "jsonEditor":
                    return(
                        <JsonEditor key={key} field={field} value={newCourse[field.name]} translations={translations} addCourseDetails={addCourseDetails}/>
                    )
                default:
                    return('')
            }
        })
    }

    validCourseParameters = (fields=[]) => {
        let valid = true;
        const {newCourse}=this.context;
        fields.forEach((field)=>{
            if(field.required && !newCourse[field.name]){
                valid=false;
            }
        })
        return valid;
    }

    render(){
        const {
            step, 
            mode, 
            selectedCourse, 
            translations, 
            handleNextClick, 
            newCourse, 
            toggleDialog, 
            handleBackClick, 
            launchCourseCreation, 
            checkCourseId
        }=this.context
        const {classes}=this.props
        const fields = this.state['step'+step]
        const disabledNext=!this.validCourseParameters(fields)
        return(
            <>
                <div className={classes.bandeau}>
                {[1,2,3,4].includes(step) ?
                    <Typography>
                        Step {step}/4
                    </Typography>
                :
                <h3 className={classes.bandeauTitle}>
                    {translations.stepFields.courseSummary}
                </h3>
                }
                    <h3 className={classes.bandeauTitle}>
                        Create Course - Mode {mode}
                    </h3>
                </div>
                <div className={classes.createCourseWrapper}>
                    {[2,3,4].includes(step) && <Cartouche translations={translations} newCourse={newCourse}/>}
                    {[1,2,3,4].includes(step) && this.createFields(fields, selectedCourse)}
                    {step===5 && <CourseSummary newCourse={newCourse} translations={translations} classes={classes}/>}
                    {step===6 && <LoadingPage classes={classes} translations={translations}/>}                 
                </div>

                {[1,2,3,4,5].includes(step) &&
                <div className={classes.createButtonsWrapper}>

                {step===1 &&
                    <Button
                        disabled={disabledNext}
                        onClick={()=>checkCourseId()}
                        className={disabledNext ? classes.disabledBtn : ''}
                    >
                        {translations["next"]}
                    </Button>
                }
                {[2,3,4].includes(step) &&
                    <Button
                        disabled={disabledNext}
                        onClick={()=>handleNextClick()}
                    >
                        {translations["next"]}
                    </Button>
                }
                {step===5 &&
                    <Button
                        disabled={disabledNext}
                        onClick={(e)=>launchCourseCreation(e)}
                    >
                        {translations["createCourse"]}
                    </Button>
                }

                {[2,3,4,5].includes(step) && 
                    <Button
                        onClick={()=>handleBackClick()}
                        className={classes.backButton}
                    >
                        {translations["back"]}
                    </Button>
                }
                <Button
                    className={classes.cancelButton}
                    onClick={()=>toggleDialog()}
                >
                    {translations["cancel"]}
                </Button>
                
                </div>  
                } 
            </>
        )
    }
}
CourseWizard.contextType = Context;
export default withStyles(styles)(CourseWizard)


